.App {
  text-align: center;
  max-width: 70%;
  margin: 0 auto;
}

.body {
  background-image: url("%PUBLIC_URL%/assets/rocks.jpeg");
  font-family: 'PT Sans', sans-serif !important;
}

.flex { display: flex; }
.f-center { justify-content: center; }
.f-vcenter { align-self: center;}
.f-around { justify-content: space-around; }
.f-wrap { flex-wrap: wrap !important; }
.mt-lg { margin-top: 48px; }
.mb-lg { margin-bottom: 48px; }
.mt-n {margin-top: 0;}
.h100 {height: 100%;}
h1, h2, h3, h4, h5, h6 { font-family: "Fugaz One", sans-serif; }
strong {font-weight: bold;}

.placeholder {
  width: 100%;
  height: 100px;
  background: magenta;
  border-radius: 16px;
}

.frame {
  border: 8px solid #558b2f;
  border-radius: 16px;
  margin: 5vh auto;
  height: 90vh;
  box-shadow: 10px 10px 24px 0px rgba(0,0,0,0.5);
}

.line {
  height: 4px;
  border-radius: 4px;
  width: 100%;
}

.navigation, .page-header {
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  font-family: "Fugaz One", sans-serif;
}

.navigation-sub {
  display: flex;
  width: 100%;
  flex-wrap: nowrap;
  justify-content: space-around;
  height: 40px;
  font-weight: normal;
}

.navigation-sub .item, .sidenav-trigger {
  width: 100%;
  padding: 8px;
  text-decoration: none;
  font-weight: bold;
  color: black;
}

.navigation-sub .item:hover {
  background: #689f38;
  color: white;
}

.page-header {
  box-shadow: 0 8px 6px -6px black;
  z-index: 10;
  height: 80px;
}

.page-header h2 {
  margin: 0; 
  padding: 16px; 
  font-size: 46px;
  color: white;
  font-weight: bold;
}

.content {
  background-color: white;
  overflow-y: scroll;
  background-color: white;
  height: calc(90vh - 136px);
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
  padding-top: 24px;
}

.btn-project {
  padding: 8px;
  border-radius: 8px;
  border: 1px solid white;
  margin: 4px 16px;
  font-size: 16px;
}

.mini-projects {
  border: 2px solid #558b2f;
  border-radius: 8px;
  margin: 16px auto;
  max-width: 600px;
  min-width: 200px;
  max-height: 600px;
  min-height: 200px;
  padding: 16px;
}

.about-img {
  width: 100%;
  max-width: 600px;
  width: 100%;
  height: auto;
  border-radius: 8px;
}

.row {
  display: flex;
  flex-wrap: wrap;
}

.spreadsheet-frame {
  min-width: 320px;
  width: 90%;
  height: 60vh;
}

.content-message {
  max-width: 500px;
  justify-content: center;
  align-items: center;
}

.btn {
  margin: 10px;
}

.big-font {
  font-size: 24px;
  border: 2px solid;
  border-radius: 8px;
  padding: 8px;
}

.big-font strong {
  font-weight: bold;
  font-size: 32px;
  text-decoration: underline;
}

.about-fun-fact {
  font-size: 16px;
  border: 2px solid #558b2f;
  border-radius: 8px;
  padding: 0;
  margin-top: 16px;
}

.fun-fact-header {
  font-weight: bold;
  font-size: 24px;
  margin: 0;
  padding: 8px;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  color: white;
}

.fun-fact-content {
  margin: 0;
  padding: 8px;
  border-bottom-left-radius: 6px;
  border-bottom-right-radius: 6px;
}

.circle {
  width: 200px;
  height: 200px;
  display: flex;
  align-items: center;
  align-self: center;
  justify-content: center;
}

.contact-icon {
  width: 100px;
  height: 100px;
  color: white;
}

.circle:hover {
  background: #c5e1a5 !important; 
}

.circle:hover .contact-icon {
  color: #689f38;
}

.sheet-subheading, .contact-type h4 {
  font-family: "Fugaz One", sans-serif;
}

.sidenav-trigger {
  display: none;
}

.resume-left {
  text-align: left;
  margin: 8px;
}

h6 {text-decoration: underline; margin-top: 0;}

.website-link {
  text-decoration: underline;
  color: #33691e;
}

.resume-img {
  max-height: 200px;
}





@media (max-width: 576px) {
  .sidenav-trigger {
    display: block;
  }
  
  .navigation-sub .item {
    display: none;
  }

  .page-header h2 {font-size: 24px;}

  .circle {
    width: 100px;
    height: 100px;
    margin: 24px;
  }

  .contact-icon {
    width: 50px;
    height: 50px;
  }

  .contact-type h4 {
    font-size: 24px;
  }
}

@media (max-width: 768px) {
  .App {
    max-width: 90%;
  }
}
